import * as React from 'react'
import { Link } from 'gatsby'

import { Row, Box, Text, ButtonElement, Media } from '@flame-ui/react'

import { Container } from '../ui/Container'
import { Logo, LogoWithText } from './Logo'

const MenuButton = ({ onClick }: { onClick: () => void }) => {
  // console.log()
  return (
    <ButtonElement onClick={onClick}>
      <Text color="white">Menu</Text>
    </ButtonElement>
  )
}

const Header = ({ onClickMenu }: { onClickMenu: () => void }) => {
  return (
    <header>
      <Box bg="primary" color="white">
        <Container>
          <Row alignItems="center" justifyContent="space-between" height="64px">
            <Media q={'(max-width: 1000px)'}>
              <Box flex={1}>
                <MenuButton onClick={onClickMenu} />
              </Box>
            </Media>
            <Link to="/">
              <Media q={'(min-width: 1000px)'}>
                <LogoWithText height={24} />
              </Media>
              <Media q={'(max-width: 1000px)'}>
                <Logo size={24} />
              </Media>
            </Link>
            <Row justifyContent="flex-end" flex={1}>
              <nav>
                <Media q={'(min-width: 1000px)'}>
                  <Row gap={3}>
                    <Text>Get started</Text>
                    <Text>Components</Text>
                    <Text>Recipes</Text>
                    <Text>Sources</Text>
                  </Row>
                </Media>
              </nav>
            </Row>
          </Row>
        </Container>
      </Box>
    </header>
  )
}

export default Header
