import * as React from 'react'
import { graphql, Link } from 'gatsby'
import { MDXRenderer } from 'gatsby-plugin-mdx'
// import MDXRenderer from 'gatsby-mdx/mdx-renderer'

import { styled, Box, Column, Row, Text } from '@flame-ui/react'

import Header from '../components/main/Header'
import Nav from '../components/main/Nav'

interface IndexPageProps {
  readonly data: GatsbyTypes.MDXPostQueryQuery
  readonly pageContext: {
    id: string
    previous?: {
      fields: { slug: string }
      frontmatter: { title: string }
    }
    next?: {
      fields: { slug: string }
      frontmatter: { title: string }
    }
  }
}

const PrevButton: React.FC<{ to: string }> = ({ to, children }) => {
  return (
    <Link to={to}>
      <Text fontSize="caption" color="primary">
        Previous
      </Text>
      <Text fontSize="body" fontWeight={500}>
        {children}
      </Text>
    </Link>
  )
}

const NextButton: React.FC<{ to: string }> = ({ to, children }) => {
  return (
    <Link to={to}>
      <Column alignItems="flex-end">
        <Text fontSize="caption" color="primary">
          Next
        </Text>
        <Text fontSize="body" fontWeight={500}>
          {children}
        </Text>
      </Column>
    </Link>
  )
}

const Overlay = () => {
  return <Box position="absolute" height="100%" width="100%" bg={'#152a4ff2'} />
}

const MainView = styled(Box)`
  transition: all 0.2s ease-in-out;
`

const NAV_WIDTH = '210px'

const Layout = ({ pageContext, data: { mdx } }: IndexPageProps) => {
  const [isOpenNav, setOpenNav] = React.useState(false)
  const { previous, next } = pageContext
  const changeMenu = () => {
    setOpenNav(!isOpenNav)
  }
  return (
    <Column height="100vh">
      <Box position="sticky" top="0" zIndex="100">
        <Header onClickMenu={changeMenu} />
      </Box>
      <MainView style={isOpenNav ? { transform: 'translate(210px)' } : {}}>
        {isOpenNav && <Overlay />}
        <Row>
          <Box ml={'-' + NAV_WIDTH}>
            <Box position="sticky" top="48px">
              <Nav width={NAV_WIDTH} />
            </Box>
          </Box>
          <Box flex="1 1" px={5} my={5}>
            <h1>{mdx.frontmatter.title}</h1>
            <MDXRenderer>{mdx.body}</MDXRenderer>
            <hr />
            <Box my={4}>
              <Row justifyContent={'space-between'}>
                <div>
                  {previous && (
                    <PrevButton to={previous.fields.slug}>{previous.frontmatter.title}</PrevButton>
                  )}
                </div>
                <div>
                  {next && <NextButton to={next.fields.slug}>{next.frontmatter.title}</NextButton>}
                </div>
              </Row>
            </Box>
          </Box>
        </Row>
      </MainView>
    </Column>
  )
}

export default Layout

export const pageQuery = graphql`
  query MDXPostQuery($id: String) {
    mdx(id: { eq: $id }) {
      id
      frontmatter {
        title
      }
      body
    }
  }
`
