import * as React from 'react'
import { Link, useStaticQuery, graphql } from 'gatsby'
import { useLocation } from '@reach/router'

import { styled, Box, Column, Text, useTheme, lighten } from '@flame-ui/react'

const NavItemLabel = styled(Text).attrs(() => ({
  fontSize: 'small',
  lineHeight: '40px',
  px: 3,
}))`
  transition-property: color;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
  &:hover {
    color: ${({ theme }) => theme.colors.primary};
  }
`

const NavItem: React.FC<{ to: string }> = ({ to, children }) => {
  const { pathname } = useLocation()
  const isCurrent = new RegExp(`^${to.replace(new RegExp('/$'), '')}.*`).test(pathname)
  const { colors } = useTheme()
  const activeColor = lighten(0.4, colors.primary)

  return (
    <Link to={to}>
      <Box
        borderRadius={3}
        bg={isCurrent ? activeColor : 'transparent'}
        color={isCurrent ? colors.primary : 'gray'}
      >
        <NavItemLabel>{children}</NavItemLabel>
      </Box>
    </Link>
  )
}

interface NavStackProps {
  label: string
  data: {
    id: string
    label: string
    url: string
  }[]
}

const Stack = ({ label, data }: NavStackProps) => {
  const { pathname } = useLocation()
  const isCollapsed = pathname.split('/')[1] !== data[0].url.split('/')[1]

  return (
    <div>
      <Link to={data[0].url}>
        <Box my={3}>
          <Text fontSize="small" fontWeight={600}>
            {label}
          </Text>
        </Box>
      </Link>
      {!isCollapsed &&
        data.map(({ label, url, id }: any) => (
          <NavItem key={id} to={url}>
            {label}
          </NavItem>
        ))}
    </div>
  )
}

const Nav = ({ width = '210px' }) => {
  const data = useStaticQuery<GatsbyTypes.AllMdxQuery>(graphql`
    query AllMdx {
      allMdx(
        filter: { frontmatter: { category: { eq: "docs" } } }
        sort: { fields: fileAbsolutePath }
      ) {
        nodes {
          fields {
            slug
          }
          id
          frontmatter {
            title
          }
        }
      }
    }
  `)

  const getNavList = (key: string) => {
    const slugRegExp = new RegExp(key.replace(' ', '-').toLowerCase() + '/')
    const list = data.allMdx.nodes.filter((el) => {
      return slugRegExp.test(el.fields.slug)
    })

    return list.map(({ id, fields, frontmatter }: any) => ({
      id,
      label: frontmatter.title,
      url: fields.slug,
    }))
  }

  return (
    <nav>
      <Box width={width} py={4}>
        <Column gap={2} mx={3}>
          <Stack label="Getting started" data={getNavList('get-started')} />
          <Stack label="Components" data={getNavList('components')} />
        </Column>
      </Box>
    </nav>
  )
}

export default Nav
